* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

header h1 {
  font-size: 32px;
}

main {
  max-width: 800px;
  background-color: #fff;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  cursor: default;
  transition: visibility .1s, opacity .5s;
}

.cat-container {
  text-align: center;
}

.breed-select {
  padding: 10px;
  font-size: 16px;
}

.breed-select:hover {
  cursor: pointer;
}

.loader-container {
  width: 100vw;
  height: 100vh;
  background-color: rgba(101, 111, 118, .5);
  justify-content: center;
  align-items: center;
  display: none;
  position: absolute;
}

.loader {
  color: #fff;
  width: 1em;
  height: 1em;
  text-indent: -9999em;
  border-radius: 50%;
  font-size: 10px;
  animation: mulShdSpin 1.3s linear infinite;
  position: relative;
  transform: translateZ(0);
}

@keyframes mulShdSpin {
  0%, 100% {
    box-shadow: 0 -3em 0 .2em, 2em -2em 0 0, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em;
  }

  12.5% {
    box-shadow: 0 -3em, 2em -2em 0 .2em, 3em 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  25% {
    box-shadow: 0 -3em 0 -.5em, 2em -2em, 3em 0 0 .2em, 2em 2em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0, 2em 2em 0 .2em, 0 3em 0 0, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0, 0 3em 0 .2em, -2em 2em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }

  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em, -2em 2em 0 .2em, -3em 0, -2em -2em 0 -1em;
  }

  75% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em, -3em 0 0 .2em, -2em -2em;
  }

  87.5% {
    box-shadow: 0 -3em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em, -3em 0, -2em -2em 0 .2em;
  }
}

.cat-info {
  text-align: center;
  margin-top: 40px;
  display: flex;
}

.description {
  flex-flow: column;
  gap: 30px;
  padding: 40px 60px 40px 20px;
  font-size: 20px;
  display: flex;
}

.div-temperament {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.temperament {
  font-weight: 700;
}

.cat-image {
  max-width: 45vw;
  max-height: 80vh;
  border: 5px solid #f0f0f0;
  border-radius: 5px;
  margin-left: 60px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

